html, body {
  overscroll-behavior-x: none;
}

input[type="range"] {
  height: 4px;
  appearance: none;
  -webkit-appearance: none;
  width: 250px;
}

input[type="range"]:focus {
  outline: none;
}

input[type="range"]:not(.pitchSlider)::-webkit-slider-runnable-track {
  width: 10px;
  height: 11px;
  cursor: pointer;
  animation: 0.2s;
  box-shadow: 1px 1px 1px #000000;
  background: #90c100;
  border-radius: 1px;
  border: 0px solid #010101;
}

input[type="range"]:not(.pitchSlider)::-webkit-slider-thumb {
  box-shadow: 1px 1px 1px #000031;
  border: 1px solid #00001e;
  height: 26px;
  width: 26px;
  border-radius: 15px;
  background: blue;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -8px;
}

input[type="range"]:not(.pitchSlider):focus::-webkit-slider-runnable-track {
  background: #90c100;
}

input[type="range"]:not(.pitchSlider)::-moz-range-track {
  width: 100%;
  height: 4px;
  cursor: pointer;
  animation: 0.2s;
  box-shadow: 1px 1px 1px #000000;
  background: #90c100;
  border-radius: 1px;
  border: 0px solid #010101;
}

input[type="range"]:not(.pitchSlider)::-moz-range-thumb {
  box-shadow: 1px 1px 1px #000031;
  border: 1px solid #00001e;
  height: 26px;
  width: 26px;
  border-radius: 15px;
  background: #ffffff;
  cursor: pointer;
}

input[type="range"]::-ms-track {
  width: 100%;
  height: 11px;
  cursor: pointer;
  animation: 0.2s;
  background: transparent;
  border-color: transparent;
  color: transparent;
}

input[type="range"]::-ms-fill-lower {
  background: #90c100;
  border: 0px solid #010101;
  border-radius: 2px;
  box-shadow: 1px 1px 1px #000000;
}

input[type="range"]::-ms-fill-upper {
  background: #90c100;
  border: 0px solid #010101;
  border-radius: 2px;
  box-shadow: 1px 1px 1px #000000;
}

input[type="range"]::-ms-thumb {
  margin-top: 1px;
  box-shadow: 1px 1px 1px #000031;
  border: 1px solid #00001e;
  height: 26px;
  width: 26px;
  border-radius: 15px;
  background: #ffffff;
  cursor: pointer;
}

input[type="range"]:focus::-ms-fill-lower {
  background: #90c100;
}

input[type="range"]:focus::-ms-fill-upper {
  background: #90c100;
}

.lego-brick {
    display: inline-block;
    background-color: #FF0000;
    border: 1px solid #880000;
    margin: 2px;
}

.lego-size-1 { width: 20px; height: 10px; }
.lego-size-2 { width: 40px; height: 10px; }
